import React, { useState, useEffect } from 'react';
import { Actor, HttpAgent } from '@dfinity/agent';
import { AuthClient } from '@dfinity/auth-client';
import { box, randomBytes } from 'tweetnacl';
import { encodeBase64, decodeBase64 } from 'tweetnacl-util';
import styled from 'styled-components';

const Container = styled.div`
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
`;

const MessageContainer = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin: 20px 0;
  height: 400px;
  overflow-y: auto;
  background: #f5f5f5;
  padding: 20px;
  border-radius: 8px;
`;

const Message = styled.div`
  padding: 10px;
  border-radius: 8px;
  max-width: 70%;
  ${props => props.sent ? 'margin-left: auto; background: #0084ff; color: white;' : 'background: white;'}
`;

const Input = styled.input`
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 100%;
  margin: 10px 0;
`;

const Button = styled.button`
  padding: 10px 20px;
  background: #0084ff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  &:hover {
    background: #0073e6;
  }
`;

function App() {
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState('');
  const [identity, setIdentity] = useState(null);
  const [recipient, setRecipient] = useState('');
  const [keyPair, setKeyPair] = useState(null);

  useEffect(() => {
    initializeAuth();
    generateKeyPair();
  }, []);

  const initializeAuth = async () => {
    const authClient = await AuthClient.create();
    if (await authClient.isAuthenticated()) {
      handleAuthenticated(authClient);
    }
  };

  const generateKeyPair = () => {
    const generatedKeyPair = box.keyPair();
    setKeyPair(generatedKeyPair);
  };

  const handleAuthenticated = async (authClient) => {
    const identity = await authClient.getIdentity();
    setIdentity(identity);
    fetchMessages();
  };

  const login = async () => {
    const authClient = await AuthClient.create();
    await authClient.login({
      identityProvider: process.env.II_URL,
      onSuccess: () => handleAuthenticated(authClient),
    });
  };

  const encryptMessage = (message, recipientPublicKey) => {
    const ephemeralKeyPair = box.keyPair();
    const sharedKey = box.before(recipientPublicKey, ephemeralKeyPair.secretKey);
    const nonce = randomBytes(box.nonceLength);
    const encryptedMessage = box.after(
      decodeUTF8(message),
      nonce,
      sharedKey
    );
    return {
      encrypted: encodeBase64(encryptedMessage),
      nonce: encodeBase64(nonce),
      ephemeralPublicKey: encodeBase64(ephemeralKeyPair.publicKey)
    };
  };

  const decryptMessage = (encryptedData) => {
    const { encrypted, nonce, ephemeralPublicKey } = encryptedData;
    const sharedKey = box.before(
      decodeBase64(ephemeralPublicKey),
      keyPair.secretKey
    );
    const decryptedMessage = box.open.after(
      decodeBase64(encrypted),
      decodeBase64(nonce),
      sharedKey
    );
    return encodeUTF8(decryptedMessage);
  };

  const sendMessage = async () => {
    if (!newMessage.trim() || !recipient.trim()) return;

    const encryptedData = encryptMessage(newMessage, recipient);
    // Call ICP canister to send message
    // This is a placeholder - implement actual canister call
    console.log('Sending encrypted message:', encryptedData);
    
    setNewMessage('');
  };

  const fetchMessages = async () => {
    // Call ICP canister to fetch messages
    // This is a placeholder - implement actual canister call
    console.log('Fetching messages...');
  };

  return (
    <Container>
      <h1>PrivateConnect</h1>
      {!identity ? (
        <Button onClick={login}>Login with Internet Identity</Button>
      ) : (
        <>
          <Input
            placeholder="Recipient's Principal ID"
            value={recipient}
            onChange={(e) => setRecipient(e.target.value)}
          />
          <MessageContainer>
            {messages.map((msg, index) => (
              <Message key={index} sent={msg.sender === identity.getPrincipal().toString()}>
                {msg.content}
              </Message>
            ))}
          </MessageContainer>
          <Input
            placeholder="Type a message..."
            value={newMessage}
            onChange={(e) => setNewMessage(e.target.value)}
            onKeyPress={(e) => e.key === 'Enter' && sendMessage()}
          />
          <Button onClick={sendMessage}>Send</Button>
        </>
      )}
    </Container>
  );
}

export default App;
